import React, {useState} from 'react';
import BoxTitleWrapper from '@iso/components/utility/boxTitle';
import {BoxWrapper} from '@iso/components/utility/box.style';
import {FullscreenExitOutlined, FullscreenOutlined} from "@ant-design/icons";
import ReactFullScreenElement from "react-fullscreen-element";
import {Button} from "antd";
import {useSelector} from "react-redux";

/**
 * Coge el estado de la aplicacion y añade un padding al componente fullscreen segun esté colapsada o no
 * (flipa-lo)
 * @param props
 * @returns {*}
 */
export default props => {
    const {collapsed} = useSelector(state => state.App);
    const [fullscreen, setFullscreen] = useState(false);
    let icon, style = {};
    if (!fullscreen) {
        icon = <Button shape="circle" icon={<FullscreenOutlined/>} onClick={() => setFullscreen(true)}/>
    } else {
        style = {zIndex: '9999', paddingLeft: collapsed?"65px":"170px", paddingTop: "70px"};
        icon = <Button shape="circle" icon={<FullscreenExitOutlined/>} onClick={() => setFullscreen(false)}/>
    }
    return <ReactFullScreenElement allowScrollbar={true} fullScreen={fullscreen}>
        <div style={style}>
            <BoxWrapper className={`${props.className ? props.className : ''} isoBoxWrapper`}
                        style={props.style}>

                <BoxTitleWrapper title={<>{icon}&#160;{props.title}</>} subtitle={props.subtitle}/>
                {props.children}
            </BoxWrapper>
        </div>
    </ReactFullScreenElement>
}
